* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body > div {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  background-color: #eff0f4;
}
.container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.bubbles {
  position: relative;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}